import React from 'react'
import css from './SliderBanner.module.scss'
import BannerTemplate from 'components/BannerTemplate/BannerTemplate'
import Slider from 'components/Slider/Slider'
import team1 from 'assets/images/slider__item--1.png'
import team2 from 'assets/images/slider__item--2.png'
import team3 from 'assets/images/slider__item--3.png'
import team4 from 'assets/images/slider__item--4.png'
import { useDispatch } from 'react-redux'
import { setVisibleBanner } from 'store/actions'

const SliderBanner = ({ id }) => {
  const data = [
    {
      photo: team1,
      contacts: {
        name: ' ',
        rank: '  WMHUG\'S',
      },
      description: {
        caption: '    Wealth Management.',
        list: [
          `  HUG\'S,  &nbsp;   &nbsp;  &nbsp;1995&nbsp;.`,
          ` 5       .`,
          `   -  "" (2001-2011).`,
        ]
      }
    },
    {
      photo: team2,
      contacts: {
        name: ' ',
        rank: '',
      },
      description: {
        list: [
          ` &nbsp;     Private Banking   .`,
          `    &nbsp;  &nbsp;2012&nbsp;.`
        ]
      }
    },
    {
      photo: team3,
      contacts: {
        name: ' ',
        rank: '  ',
      },
      description: {
        list: [
          `  &nbsp;   15&nbsp;.    10&nbsp;   Private Banking,  &nbsp; ,  &nbsp;&nbsp; &nbsp; .`,
        ]
      }
    },
    {
      photo: team4,
      contacts: {
        name: ' ',
        rank: ' ',
      },
      description: {
        list: [
          `10-    &nbsp;   &nbsp;.`,
          `  ,  30&nbsp;  &nbsp; .`,
          `   &nbsp;  &nbsp;30% .    &nbsp;  80&nbsp;. .`,
        ]
      }
    },
  ]

  const dispatch = useDispatch()

  return (
    <>
      <BannerTemplate
        bgColorClass={css.wrapper}
        heading=' '
        slideIndex={4}
        outOfContainerElements={(
          <Slider
            data={data}
          />
        )}
        id={id}
        onLeave={evt => {
          if (evt.currentPosition === 'above') {
            dispatch(setVisibleBanner(null))
          }
        }}
      >
        <div className={css.content}>
          <div className={css.intro}>
            <p className={css.slogan}>
                 
            </p>
            <p className={css.descriptor}>
                &nbsp; . &nbsp;      &nbsp; &nbsp; 
            </p>
          </div>
        </div>
      </BannerTemplate>
    </>
  )
}

export default SliderBanner
